import React, { useState, useEffect } from 'react'
import Logo from '../../../assets/images/logo.png'
import Hamburger from '../../../assets/images/hamburger.png'

import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

library.add(fas, fab);

const Header = () => {

  const [isAwake, setAwake] = useState(false)
  const [isMenuExpanded, setMenuExpanded] = useState(false)

  const handleScroll = () => {
    const scrollOffset = window.scrollY;
    if (scrollOffset >= 150) {
      setAwake(true)
    }
    else {
      setAwake(false)
    }
  }

  const handleResize = () => {
    setMenuExpanded(false)
  }

  const handleMenu = (status) => {
    setMenuExpanded(status)
  }
  
  useEffect(() => {

    window.addEventListener('scroll', handleScroll);
    window.addEventListener('resize', handleResize);


    // Remove the event listener when the component is unmounted
    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('resize', handleResize);
    };


  }, [])

  return (
    <div className={`header ${isAwake ? 'awake' : ''}`}>
      <nav className="navbar navbar-expand-lg">
        <div className="container">
          <a className="navbar-brand" href="/#">
            <img src={Logo} alt="" />
          </a>
          <div className="navbar-toggler" onClick={() => {handleMenu(true)}}>
            <img src={Hamburger} alt="" />
          </div>
          <div className={`collapse navbar-collapse ${isMenuExpanded ? 'show' : ''}`}>
            <div className="close-menu d-flex d-lg-none">
              <div className="icon">
                <FontAwesomeIcon icon="fa-solid fa-xmark text-primary-900" onClick={() => {handleMenu(false)}} />
              </div>
            </div>
            <ul className="navbar-nav ms-auto">
              <li className="nav-item active">
					      <a className="nav-link" href="#/">Home</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#/">About</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#/">Courses</a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#/">Contact</a>
              </li>
              <li className="nav-item btn-item">
                <a className="nav-link btn rounded-full" href="#/">Register</a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  )
}

export default Header